    to bottom, 
    #a166ab, #5073b8, #1098ad, #07b39b, #6fba82, #f79533, #f37055, #ef4e7b
  ) 1 100%;
  }
  50% {
    border-image: linear-gradient(
    to bottom, 
    #5073b8, #1098ad, #07b39b, #6fba82, #f79533, #f37055, #ef4e7b, #a166ab
  ) 1 100%;
  }
  62.5% {
    border-image: linear-gradient(
    to bottom, 
    #1098ad, #07b39b, #6fba82, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8
  ) 1 100%;
  }
  75% {
    border-image: linear-gradient(
    to bottom, 
    #07b39b, #6fba82, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad
  ) 1 100%;
  }
  87.5% {
    border-image: linear-gradient(
    to bottom, 
    #6fba82, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b
  ) 1 100%;
  }
  100% {
    border-image: linear-gradient(
    to bottom, 
    #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82
