.thumb {
    height: 10em;
    width: 15em;
    background-color: #121212;
    transition: transform .2s;
    text-align: center;
    border-style: none;
    border-width: 1px;
    border-radius: 15px;
    padding-top: 25%;
    text-decoration: none;
}

.thumb:hover {
    transform: scale(1.2); 
    text-decoration: none;
}

.big-thumb {
    height: auto;
    width: 100%;
    background-color: #121212;
    transition: transform .2s;
    border-style: none;
    border-width: 1px;
    border-radius: 15px;
    padding: 3%;
    text-decoration: none;
}
